import React, { useState } from 'react';
import { TextField, Paper, Button, Grid, IconButton, Slider } from '@mui/material';
import { styled } from '@mui/system';
import GiftImage from "./images/present.png";
import LockIcon from '@mui/icons-material/Lock';
import "./TwoColumnComponent.css";
import SlotMachine from './SlotMachine';
import AutorenewIcon from '@mui/icons-material/Autorenew';
import DeleteIcon from '@mui/icons-material/Delete';

// Styled components for column widths
const LeftColumn = styled('div')({
  width: '77vw',
  overflowY: 'auto',
  scrollbarWidth: 'thin', // For Firefox
  '&::-webkit-scrollbar': {
    width: '0.5em', // Adjust as needed for WebKit browsers
  },
  '&::-webkit-scrollbar-thumb': {
    backgroundColor: 'rgba(0, 0, 0, 0.1)', // Adjust as needed
  },
});

const RightColumn = styled('div')({
  width: '23vw',
  padding: '16px',
  overflowY: 'auto',
  scrollbarWidth: 'thin', // For Firefox
  '&::-webkit-scrollbar': {
    width: '0.5em', // Adjust as needed for WebKit browsers
  },
  '&::-webkit-scrollbar-thumb': {
    backgroundColor: 'rgba(0, 0, 0, 0.1)', // Adjust as needed
  },
});

interface ListItem {
  name: string;
  iconCount: number;
}

// ... (previous imports)

const TwoColumnComponent: React.FC = () => {
  const [list, setList] = useState<ListItem[]>([]);
  const [newItemName, setNewItemName] = useState<string>('');
  const [iconCount, setIconCount] = useState<number>(3);
  const [isShowingRng, setIsShowingRng] = useState<boolean>(true);
  const [isNumParticipantsBeingSet, setIsNumParticipantsBeingSet] = useState<boolean>(true);

  const handleAddItem = () => {
    if (newItemName.trim() === '') {
      // Don't add empty items
      return;
    }

    const newItem: ListItem = {
      name: newItemName,
      iconCount,
    };

    setList([...list, newItem]);
    setNewItemName('');
    setIconCount(3);
  };

  const handleUpdateIconCount = (index: number, newIconCount: number) => {
    const updatedList = [...list];
    updatedList[index].iconCount = newIconCount;
    setList(updatedList);
  };

  const numParticipantsConfirmCallback = () => {
    setIsNumParticipantsBeingSet(false);
  };

  return (
    <div style={{ backgroundColor: '#631919'}}>
      <div style={{ display: 'flex' }}>
        {/* Left Column */}
        <LeftColumn className="noscroll" style={{ height: "100vh" }}>
          <Grid container spacing={2} style={{ paddingLeft: "10px", paddingRight: "10px", paddingTop: "10px" }}>
            {list.map((item, index) => (
              <Grid item xs={6} sm={3} key={index}>
                <Paper
                  className="fadeIn"
                  style={{
                    padding: '16px',
                    transition: 'opacity 0.5s',
                    opacity: item.iconCount > 0 ? 1 : 0.6,
                    position: 'relative',
                  }}>
                  <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', height: "50px" }}>
                    <div style={{ fontSize: "28px", width: "60%" }}>{item.name}</div>
                    <div>
                      {[...Array(3)].map((_, i) => (
                        <img
                          className="spinOut"
                          src={GiftImage}
                          alt="Gift"
                          style={{
                            transition: 'opacity 0.6s',
                            opacity: i < item.iconCount ? 1 : 0,
                            width: "30px",
                            marginRight: "10px"
                          }}
                        />
                      ))}
                    </div>
                  </div>
                </Paper>
                {
                  item.iconCount <= 0 &&
                    <LockIcon style={{ position: 'absolute', fontSize: '55px', color: 'white', translate: '150px -70px' }} />
                }
              </Grid>
            ))}
          </Grid>
          <div style={{ position: "absolute", bottom: 10, left: 10, fontWeight: 700, color: "rgba(255, 255, 255, 0.8)" }}>
            Created by Andrew Tong (Social FI US)
          </div>
        </LeftColumn>

        {/* Right Column */}
        <RightColumn className="noscroll" style={{ backgroundColor: 'white' }}>
          {
            !isNumParticipantsBeingSet &&
              <Button
                onClick={() => setIsShowingRng(!isShowingRng)}
                startIcon={<AutorenewIcon/>}
                variant="contained"
                style={{ width: "100%", marginBottom: "2vh", backgroundColor: '#631919' }}
              >
                Switch
              </Button>
          }
          <div style={{ display: isShowingRng ? 'block' : 'none' }}>
            <SlotMachine confirmSettingsCallback={numParticipantsConfirmCallback} />
          </div>
          <div style={{ display: isShowingRng ? 'none' : 'block' }}>
            <>
              <TextField
                label="New Item Name"
                value={newItemName}
                onChange={(e) => setNewItemName(e.target.value)}
                style={{ marginBottom: "10px" }}
                fullWidth
              />
              {/* <TextField
                label="Number of Icons"
                type="number"
                value={iconCount}
                onChange={(e) => setIconCount(parseInt(e.target.value, 10))}
                fullWidth
              /> */}
              <Button variant="contained" style={{ backgroundColor: "#631919" }} onClick={handleAddItem} fullWidth>
                Add Item
              </Button>
              <div style={{ width: "100%", backgroundColor: "#3b3b3b", height: "1px", margin: "10px 0 10px 0" }}></div>
              <Grid container alignItems="center" spacing={2}>
                <Grid item xs={6}>
                  <b>Item Name</b>
                </Grid>
                <Grid item xs={4}>
                  <b>Steals Left</b>
                </Grid>
                <Grid item xs={2}>
                  <b>Delete</b>
                </Grid>
              </Grid>
              {
                list.map((item, index) => (
                  <div style={{ marginTop: "12px" }}>
                    <Grid container alignItems="center" spacing={2}>
                      <Grid item xs={6}>
                        {item.name}
                      </Grid>
                      <Grid item xs={4}>
                        <Slider
                          aria-label="Steals Remaining"
                          defaultValue={3}
                          valueLabelDisplay="auto"
                          step={1}
                          marks
                          min={0}
                          max={3}
                          value={item.iconCount}
                          onChange={(event: Event, newValue: number | number[]) => {
                            handleUpdateIconCount(index, newValue as number)
                          }}
                        />
                      </Grid>
                      <Grid item xs={2}>
                        <IconButton onClick={() => {
                          const listCopy = list;
                          // Check if the index is valid
                          if (index >= 0 && index < list.length) {
                            listCopy.splice(index, 1);
                            setList([...listCopy]);
                          } else {
                            console.error('Invalid index');
                            return false; // Indicate that the removal failed due to an invalid index
                          }
                        }}>
                          <DeleteIcon />
                        </IconButton>
                      </Grid>
                    </Grid>
                  </div>
                ))
              }
            </>
          </div>
        </RightColumn>
      </div>
    </div>
  );
};

export default TwoColumnComponent;
