// SlotMachine.tsx

import React, { useState, useEffect } from 'react';
import './SlotMachine.css';
import { Button, TextField } from '@mui/material';

interface SlotMachineInterface {
  confirmSettingsCallback: () => void;
}

const SlotMachine = (props: SlotMachineInterface) => {
  const initialParticipants = 60;
  const { confirmSettingsCallback } = props;

  const [seed, setSeed] = useState<string>("TikTokWhiteElephant");
  const [resumeCount, setResumeCount] = useState<number>(0);
  const [isSettingParticipants, setIsSettingParticipants] = useState<boolean>(true);
  const [numParticipants, setNumParticipants] = useState<number>(initialParticipants);
  const [randomNumber, setRandomNumber] = useState<number | null>(null);
  const [trueNumber, setTrueNumber] = useState<number | null>(null);
  const [isSpinning, setIsSpinning] = useState<boolean>(false);
  const [numberOrder, setNumberOrder] = useState<number[]>([]);
  const [currIndex, setCurrIndex] = useState<number>(numParticipants - 1);
  const [gameOver, setGameOver] = useState<boolean>(false);

  // Generates randomly shuffled array on start
  useEffect(() => {
    const seedrandom = require('seedrandom');
    let currSeed = seedrandom(seed)();

    function generateRandomArray(arraySize: number) {
      const array = Array.from({ length: arraySize }, (_, index) => index + 1);
      var m = array.length, t, i;

      // While there remain elements to shuffle…
      while (m) {

        // Pick a remaining element…
        i = Math.floor(random(currSeed) * m--);

        // And swap it with the current element.
        t = array[m];
        array[m] = array[i];
        array[i] = t;
        ++currSeed
      }

      return array;
    }

    function random(seed: number) {
      var x = Math.sin(seed++) * 10000;
      return x - Math.floor(x);
    }

    const playerOrder = generateRandomArray(numParticipants);
    console.log(playerOrder);
    setNumberOrder(playerOrder);
    setCurrIndex(numParticipants - 1 - resumeCount);
  }, [numParticipants, resumeCount, seed]);

  // Handles spinning animation
  useEffect(() => {
    let intervalId: NodeJS.Timeout;

    if (isSpinning) {
      intervalId = setInterval(() => {
        const randomIndex = Math.round(Math.random() * currIndex);
        // console.log(`numParticipants: ${numParticipants} | currIndex: ${currIndex} | rand: ${Math.random() * currIndex}`);
        setRandomNumber(numberOrder[randomIndex]);
      }, 100);
    }

    return () => clearInterval(intervalId);
  }, [currIndex, isSpinning, numberOrder, numParticipants]);

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const inputValue = parseInt(event.target.value, 10);
    setNumParticipants(isNaN(inputValue) ? 0 : inputValue);
  };

  const handleInputChangeResumeCount = (event: React.ChangeEvent<HTMLInputElement>) => {
    const inputValue = parseInt(event.target.value, 10);
    setResumeCount(isNaN(inputValue) ? 0 : inputValue);
  };

  const handleInputChangeSeed = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSeed(event.target.value);
  };

  const handleSpinClick = () => {
    if (!isSpinning) {
      setIsSpinning(true);
      setTrueNumber(null);

      // Set a timeout to stop spinning after a random duration
      setTimeout(() => {
        setIsSpinning(false);

        if (currIndex > 0) {
          setTrueNumber(numberOrder[currIndex]);
          setCurrIndex(currIndex - 1);
        } else {
          setGameOver(true);
        }
      }, 3000); // Adjust the duration as needed
    }
  };

  return (
    <div>
      {
        isSettingParticipants ?
          <>
            <TextField
              label="Number of Participants"
              value={numParticipants}
              onChange={handleInputChange}
              style={{ marginBottom: "10px" }}
              fullWidth
            />
            {/* <TextField
              label="Resume Index"
              value={resumeCount}
              onChange={handleInputChangeResumeCount}
              style={{ marginBottom: "10px" }}
              fullWidth
            /> */}
            <TextField
              label="Seed"
              value={seed}
              onChange={handleInputChangeSeed}
              style={{ marginBottom: "10px" }}
              fullWidth
            />
            <Button
              variant="contained"
              fullWidth
              onClick={() => {
                setIsSettingParticipants(false)
                confirmSettingsCallback()
              }}
              style={{ backgroundColor: "#631919" }}
            >
              Confirm Settings
            </Button>
          </>
          :
          <div className="slot-machine">
            <button className="spin-button btn-grad" onClick={handleSpinClick}>
              <div className="reel">
                { trueNumber !== null ? trueNumber : randomNumber }
              </div>
              <div className="button-text" style={{ marginTop: "10px" }}>
                GIFTS REMAINING: { currIndex + 1 }
              </div>
            </button>
            {/* {
              !gameOver &&
                <button onClick={handleSpinClick} disabled={isSpinning}>
                  Spin
                </button>
            } */}
          </div>
      }
    </div>
  );
};

export default SlotMachine;
